import { Component, OnInit } from '@angular/core';
import {MenuMaster} from '../../KOT/menu-master/menu-master';
import {MenuMasterService} from '../../KOT/menu-master/menu-master.service';
import {DashboardService} from '../dashboard.service';
import {TempRecipes} from '../temp-recipes';
@Component({
  selector: 'app-inversamount',
  templateUrl: './inversamount.component.html',
  styleUrls: ['./inversamount.component.css']
})
export class InversamountComponent implements OnInit {

  private menuMaster:MenuMaster[];
  private tempRecipes:TempRecipes[];
  private inverseAmount;
  private salesAmount;
  constructor(private menuMasterService:MenuMasterService,
    private dashboardService: DashboardService) { }
  ngOnInit() {
    this.getOverAllSalesAmount();
    this.getTotalInversAmount();
  }

  getOverAllSalesAmount(){
    this.salesAmount=0;
    this.dashboardService.getDayMenuMaster().subscribe((data)=>{
      this.menuMaster=data;
      this.menuMaster.forEach(res=>{
        let amount=res.basicAmount*res.availableMenuQuantity;
        this.salesAmount=this.salesAmount+amount;
      })
    },(error)=>{
      console.log(error);
    });
  }
  getTotalInversAmount(){
    this.inverseAmount=0;
    this.dashboardService.getAllTempRecipes().subscribe((data)=>{
      this.tempRecipes=data;
      this.tempRecipes.forEach(res=>{
        this.inverseAmount=this.inverseAmount+res.totalAmount;
      });
    },(error)=>{
      console.log(error);
    });
  }
}
