import { Component, OnInit } from '@angular/core';
import { Chart } from 'chart.js';
import { DatePipe } from '@angular/common';
import {DashboardService} from '../dashboard.service';
import {Purchase} from '../../inventory/purchase/purchase';
@Component({
  selector: 'app-purchase-chart',
  templateUrl: './purchase-chart.component.html',
  styleUrls: ['./purchase-chart.component.css'],
  providers: [DatePipe]
})
export class PurchaseChartComponent implements OnInit {

  private purchase:Purchase[];
  private date;
  private time;
  LineChart: any;
  private day=[];
  private amount=[];
  private amount1=[];
  private amount2=[];
  private Week=[];
  private month=[];
  constructor(private datePipe: DatePipe,
    private dashboardService: DashboardService) { }

  ngOnInit() {
    this.date = this.datePipe.transform(new Date(), 'EE');
    this.time = this.datePipe.transform(new Date(), 'HH:mm:ss');
    this.getDay();
    this.getPurchaseAmountByDay();
  }

  getDay(){
    if(this.date=='Sun'){
      this.day=['Mon','Tue','Wed','Thurs','Fri','Sat','Sun']
    }else if(this.date=='Mon'){
      this.day=['Tue','Wed','Thurs','Fri','Sat','Sun','Mon']
    }else if(this.date=='Tue'){
      this.day=['Wed','Thurs','Fri','Sat','Sun','Mon','Tue']
    }else if(this.date=='Wed'){
      this.day=['Thurs','Fri','Sat','Sun','Mon','Tue','Wed']
    }else if(this.date=='Thurs'){
      this.day=['Fri','Sat','Sun','Mon','Tue','Wed','Thurs']
    }else if(this.date=='Fri'){
      this.day=['Sat','Sun','Mon','Tue','Wed','Thurs','Fri']
    }else if(this.date=='Sat'){
      this.day=['Sun','Mon','Tue','Wed','Thurs','Fri','Sat']
    }
  }


  getPurchaseAmountByDay(){
    this.dashboardService.getPurchaseAmountByDay().subscribe((data)=>{
      this.purchase=data;
      this.amount=[];
      this.purchase.forEach(res=>{
        this.amount.push(res.totalAmount);
      });
      this.LineChart = new Chart('lineChart', {
        type: 'line',
        data: {
          labels:this.day, //["06:AM","07:AM","08:AM","09:AM","10:AM","11:AM","12:PM","1:PM","2:AM","3:AM","4:PM","5:PM","06:PM","07:PM","08:PM","09:PM","10:PM","11:PM"],
          datasets: [
            {
              label: 'Purchase Amount By Day Wise',
              data: this.amount,
              fill: false,
              borderColor: "red",
              borderWidth: 1
            }],
        },
        options: {
          title: {
            text: "Puchase Chart",
            display: true
          },
          legend: {
            display: true
          },
          scales: {
            yAxes: [{
              id: 'A',
              type: 'linear',
              position: 'left',
              ticks: {
                beginAtZero: true
              }
            }],
          }
        }
      });
    },(error)=>{
      console.log(error);
    });
  }

  getPurchaseAmountByWeek(){
    this.dashboardService.getPurchaseAmountByWeek().subscribe((data)=>{
      this.purchase=data;
      this.amount1=[];
      this.Week=[];
      this.purchase.forEach(res=>{
        this.amount1.push(res.totalAmount);
        this.Week.push(res.weekOfMonth);
      });
      this.LineChart = new Chart('lineChart', {
        type: 'line',
        data: {
          labels:this.Week, //["06:AM","07:AM","08:AM","09:AM","10:AM","11:AM","12:PM","1:PM","2:AM","3:AM","4:PM","5:PM","06:PM","07:PM","08:PM","09:PM","10:PM","11:PM"],
          datasets: [
            {
              label: 'Purchase Amount By Week Wise',
              data: this.amount1,
              fill: false,
              borderColor: "red",
              borderWidth: 1
            }],
        },
        options: {
          title: {
            text: "Puchase Chart",
            display: true
          },
          legend: {
            display: true
          },
          scales: {
            yAxes: [{
              id: 'A',
              type: 'linear',
              position: 'left',
              ticks: {
                beginAtZero: true
              }
            }],
          }
        }
      });
    },(error)=>{
      console.log(error);
    });
  }
  getPurchaseAmountByMonth(){
    this.dashboardService.getPurchaseAmountByMonth().subscribe((data)=>{
      this.purchase=data;
      this.amount2=[];
      this.month=[];
      this.purchase.forEach(res=>{
        this.amount2.push(res.totalAmount);
        this.month.push(res.weekOfMonth);
      });
      this.LineChart = new Chart('lineChart', {
        type: 'line',
        data: {
          labels:this.month, //["06:AM","07:AM","08:AM","09:AM","10:AM","11:AM","12:PM","1:PM","2:AM","3:AM","4:PM","5:PM","06:PM","07:PM","08:PM","09:PM","10:PM","11:PM"],
          datasets: [
            {
              label: 'Purchase Amount By Month Wise',
              data: this.amount2,
              fill: false,
              borderColor: "red",
              borderWidth: 1
            }],
        },
        options: {
          title: {
            text: "Puchase Chart",
            display: true
          },
          legend: {
            display: true
          },
          scales: {
            yAxes: [{
              id: 'A',
              type: 'linear',
              position: 'left',
              ticks: {
                beginAtZero: true
              }
            }],
          }
        }
      });
    },(error)=>{
      console.log(error);
    });
  }
}
